.app{
  display: grid;
  grid-template-columns: auto auto;
  justify-content: start;
  /* place-items: center; */
  /* height: 100vh; */
  background-color: rgba(255, 255, 255,1);
  font-family: 'Raleway', sans-serif;
}

.app_body{
  display: flex;
  background-color: #ededed;
  margin-top: -50px;
  height:90vh;
  width: 90vw;
  box-shadow: -1px 4px 20px -6px rgba(0,0,0,0.75);
}

.app_center{
  text-align: center;
  margin-top: -50px;
}

.sidebar{
  display: inline-grid;
  flex-direction: column;
  flex: 0.35 1;
  background-color: rgba(240, 242, 245,1);
  justify-content: space-between;
  padding: 20px;
  /* padding-left: 40px; */
  /* border: 1px solid red; */
  text-align: center;
  height: 100vh;
  overflow: scroll;
}

.sidebar__img{
  border-radius: 50%;
  border: 1px solid lightgray;
}

ul{
  list-style:none;
}
ul,li{
  margin: 0;
  padding: 0;
}

.sidebar__header{
  /* display: flex; */
  text-align: right;
  /* justify-content: space-between; */
  /* min-width: 10vw; */
}

.sidebar__social{
  margin-bottom: 20px;
}
.sidebar__social > li{
  margin-top: 10px;
}

